<template>
    <button
        class="SmallButton"
        :type="type"
        :class="computedClass"
        @click="click"
    >
        <p class="label">
            <slot></slot>
        </p>
        <font-awesome-icon v-if="icon" :icon="icon" />
    </button>
</template>

<script>
export default {
    name: 'SmallButton',
    props: {
        borderColor: {
            type: String,
            default: 'white',
        },
        color: {
            type: String,
            default: null,
        },
        icon: {
            default: null,
        },
        type: {
            type: String,
            default: 'button',
        },
    },
    computed: {
        computedClass() {
            return {
                [`border-${this.borderColor}`]: true,
                [this.color]: true,
                hasIcon: this.icon != null,
            };
        },
    },
    methods: {
        click() {
            this.$emit('click');
        },
    },
};
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';
    @import "@/assets/scss/bulma/projectBulma.scss";

    .SmallButton {
        user-select: none;
        font-family: "Roboto", sans-serif;
        padding: 5px;
        min-width: 100px;
        background-color: transparent;
        border: solid 1px $primary;
        color: $dark-text;
        font-size: 0.8em;
        font-weight: normal;
        outline: none;
        cursor: pointer;

        .label {
            padding: 0;
            margin: 0 !important;
            font-size: 1em;
            color: white;
            font-weight: 400;
        }

        svg {
            display: none;
            transition: 0.05s ease-out color;
        }

        &.border-white {
            border-color: $white;
        }

        &.border-primary {
            border-color: $primary;
        }

        &.border-danger {
            border-color: red;
        }

        &.primary {
            background-color: $primary;
        }
    }

    // Mobile style
    @media (max-width: $phone-max-width) {
        .SmallButton.hasIcon {
            border: none;
            min-width: unset;
            font-size: 1.5em;

            .label {
                display: none;
            }

            svg {
                display: block;
            }

            &:hover {
                color: #aaa;
            }
        }
    }
</style>
