<template>
    <button
        class="AuthButton"
        :type="type"
        :style="computedStyle"
        @click="click"
    >
        <img v-if="icon" :src="icon" />
        <p class="label">
            <slot></slot>
        </p>
    </button>
</template>

<script>
export default {
    name: 'AuthButton',
    props: {
        color: {
            type: String,
            default: null,
        },
        icon: {
            default: null,
        },
        type: {
            type: String,
            default: 'button',
        },
    },
    computed: {
        computedStyle() {
            return {
                backgroundColor: this.color,
            };
        },
    },
    methods: {
        click() {
            this.$emit('click');
        },
    },
};
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/_variables.scss';

    .AuthButton {
        user-select: none;
        font-family: "Roboto", sans-serif;
        padding: 16px;
        min-width: 100px;
        background-color: transparent;
        border: none;
        color: $dark-text;
        font-size: 0.8em;
        outline: none;
        cursor: pointer;

        .label {
            padding: 0;
            margin: 0 !important;
            font-size: 1em;
            color: white;
            font-weight: bold;
        }

        img {
            display: none;
            transition: 0.05s ease-out color;
        }
    }
</style>
